import React, { memo, useState, useRef, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { createImpchar } from '../../redux/addchar/action.js'
import { connect } from 'react-redux'
import Dialog from '../Dialog'

import './index.css'
const TopBar = memo(function TopBar({ impchar, chars }) {

  const [jieshao, setJishao] = useState('')
  const [color, setColor] = useState(-1)
  const [show, setShow] = useState(false)
  const [showDiolag, setShowDiolag] = useState(false)
  const [dioContent, setDioContent] = useState('')
  const [inpshow, setInpshow] = useState(false)

  const stand = ["id", "baseBonusType", "infor", "moonlight", "star", "occ", "cName", "charIcon", "base", "base_userAtk", "base_userHp", "baseBonus", "baseBonus_atk", "baseBonus_hp", "base_speed", "base_def", "base_critRate", "base_critDamage", "base_effect", "base_resist", "skill", "artifact", "artifact_atk", "artifact_hp", "artifact_critRate", "artifact_critDamage", "sssAttr", "sssAttr_atk", "sssAttr_def", "sssAttr_hp", "sssAttr_effect", "sssAttr_resist", "sssAttr_critRate", "sssAttr_critDamage", "sssAttr_speed", "extraAttr", "extraAttr_atk", "extraAttr_def", "extraAttr_hp", "extraAttr_effect", "extraAttr_resist", "extraAttr_critRate", "extraAttr_critDamage", "extraAttr_speed", "outfit", "outfit_suit", "outfit_main", "outfit_rank", "outfit_quality", "outfit_sword", "outfit_head", "outfit_armor", "outfit_necklace", "outfit_ring", "outfit_shoe", "outfit_swordUser", "outfit_headUser", "outfit_armorUser", "outfit_necklaceUser", "outfit_ringUser", "outfit_shoeUser", "outfit_sword85", "outfit_head85", "outfit_armor85", "outfit_necklace85", "outfit_ring85", "outfit_shoe85", "buff", "buff_atk", "buff_def", "buff_speed", "buff_critDamage", "buff_addDamage", "buff_target", "buff_refrain", "buff_isAtkGrowth", "buff_iscritDamageGrowth", "buff_isSpeedGrowthFinal", "buff_isSpeedGrowthBase", "buff_isDefGrowth", "debuff", "debuff_bleed", "debuff_burn", "debuff_bomb", "choose", "Jchoose", "enemyWait", "enemyWait_def", "enemyBuff", "enemyBuff_def", "enemyFinal", "enemyFinal_reduce", "enemyFinal_guard", "enemyFinal_hp", "add", "add_bleed", "add_burn", "add_bomb", "add_joker", "add_drink", "damage", "debuffA", "debuffJA", "debuffJB", "debuffJNum", "way", "usual", "miss"]
  const skill = ["sName", "s_addDamage", "fixedType", "mag", "POW", "MAG", "SoulMAG", "fixed", "fixed_myHp", "fixed_loseMyHp", "fixed_myDef", "chooseThroughout", "chooseThroughout_is", "chooseThroughout_normal", "chooseThroughout_suit", "chooseDamage", "type", "chooseDamage_is", "chooseDamage_normal", "chooseDamage_per", "s_addDamageA", "s_add", "s_throughout", "s_damage", "s_fix", "s_critDamage", "s_jokerLike"]

  const input = useRef()
  const a = useRef()

  function getData() {
    let res = window.localStorage.getItem('E7SColor')
    // console.log(res);
    if (res !== null) {
      return res * 1
    } else {
      return 0
    }
  }
  function setData(data) {
    window.localStorage.setItem('E7SColor', data)
  }

  useEffect(() => {
    setColor(getData())
  }, [])

  function changeColor() {
    if (color === 0) {
      setColor(1); setData(1)
    }
    if (color === 1) {
      setColor(2); setData(2)
    }
    if (color === 2) {
      setColor(0); setData(0)
    }
  }
  // 修改颜色
  useEffect(() => {

    if (color === 1) {
      window.document.documentElement.style.setProperty('--background', '#f3fdf4')
      window.document.documentElement.style.setProperty('--btn', '#92c76e')
      window.document.documentElement.style.setProperty('--theme', '#689550')
      window.document.documentElement.style.setProperty('--boxshawo', '#689550')
      window.document.documentElement.style.setProperty('--border', 'transition')
      window.document.documentElement.style.setProperty('--wBorder', '#b9dca4')
      window.document.documentElement.style.setProperty('--wBorder2', '#5b8d39')
      window.document.documentElement.style.setProperty('--cColor', '#d9e6e0')
      window.document.documentElement.style.setProperty('--cColor2', '#dad4ba')
      window.document.documentElement.style.setProperty('--danger', '#c75d5f')
    }

    if (color === 2) {
      window.document.documentElement.style.setProperty('--background', '#f1eee9')
      window.document.documentElement.style.setProperty('--btn', '#fda359')
      window.document.documentElement.style.setProperty('--theme', '#a73434')
      window.document.documentElement.style.setProperty('--boxshawo', '#bd2a22')
      window.document.documentElement.style.setProperty('--border', 'transition')
      window.document.documentElement.style.setProperty('--wBorder', '#c5996d')
      window.document.documentElement.style.setProperty('--wBorder2', '#8f573e')
      window.document.documentElement.style.setProperty('--cColor', '#f0ebdb')
      window.document.documentElement.style.setProperty('--cColor2', '#c6dfdb')
      // window.document.documentElement.style.setProperty('--danger', '#207c88')
      window.document.documentElement.style.setProperty('--danger', '#537b35')
    }

    if (color === 0) {
      window.document.documentElement.style.setProperty('--background', '#e1ebe3')
      window.document.documentElement.style.setProperty('--btn', '#c6dbda')
      window.document.documentElement.style.setProperty('--theme', '#1eb1a7')
      window.document.documentElement.style.setProperty('--boxshawo', 'transition')
      window.document.documentElement.style.setProperty('--border', '#1eb1a7')
      window.document.documentElement.style.setProperty('--wBorder', '#67afaab0')
      window.document.documentElement.style.setProperty('--wBorder2', '#1eb1a7b0')
      window.document.documentElement.style.setProperty('--cColor', '#f3e1dd')
      window.document.documentElement.style.setProperty('--cColor2', '#dcbe95')
      window.document.documentElement.style.setProperty('--danger', '#fa635a')
    }
  }, [color])

  function come(e) {
    if (e.target.innerHTML === '导出数据') {
      setJishao('导出所有的数据，你可以将这些数据分享给其他人，或在其他设备中重新导入')
      setInpshow(false)
    } else {
      setJishao('导入的数据不会覆盖原有数据，请在导入后自行判断数据留存')
      setInpshow(true)
    }
  }

  function showbar() {
    setShow(!show)
  }

  function isJSON(str) {
    if (typeof str == 'string') {
      try {
        var obj = JSON.parse(str);
        if (typeof obj == 'object' && obj) {
          return true;
        } else {
          return false;
        }

      } catch (e) {
        // console.log('error：' + str + '!!!' + e);
        setShowDiolag(true)
        setDioContent('错误的文件')
        // alert('错误的文件')
        input.current.value = ''
        return false;
      }
    }
    setShowDiolag(true)
    setDioContent('错误的文件')
    // alert('错误的文件')
    input.current.value = ''
  }

  function downloadFile(name, data) {
    a.current.href = window.URL.createObjectURL(data)
    a.current.download = name
    a.current.click()
    window.URL.revokeObjectURL(data)
  }

  function exportData(name, data) {
    const content = JSON.stringify(data)
    // const blobData = new Blob([content], { type: 'application/octet-stream' })
    const blobData = new Blob([content])
    const filename = name + '.txt'
    downloadFile(filename, blobData)
    // downloadFile(filename, content)
  }
  // 导出
  function exp() {
    // let res = checkProp(chars[0].skill[0])
    // console.log(res);
    // exportData('E7Simulator', res)

    exportData('E7Simulator', chars)
  }


  function checkProp(data) {
    const res = []
    Object.getOwnPropertyNames(data).forEach(e => {
      res.push(e)
      if (data[e]?.constructor === Object) {
        res.push(...checkProp(data[e]))
      }
    });
    return res
  }

  // 导入
  function imp() {
    //判断浏览器是否支持FileReader接口  
    if (typeof window.FileReader == 'undefined') {
      setShowDiolag(true)
      setDioContent('该浏览器不支持导入文件！')
      // alert('该浏览器不支持导入文件！')
      return false
    }
    setJishao('正在检测数据合法性...')
    if (input.current.value !== '') {
      const file = input.current.files[0];
      const reader = new FileReader();
      //将文件以二进制形式读入页面  
      reader.readAsText(file);
      reader.onload = function (f) {
        //显示文件  
        if (isJSON(this.result)) {
          const res = JSON.parse(this.result)
          // console.log(res)

          let flag = false

          const char = res.filter((c) => {

            const arr = checkProp(c)
            let res2 = stand.every((p) => {
              if (arr.includes(p)) return true
              else {
                flag = true
                return false
              }
            })

            let res3 = true
            if (c.skill.length > 0) {
              c.skill.map((s) => {
                const arr2 = checkProp(s)
                res3 = skill.every((p) => {
                  if (arr2.includes(p)) return true
                  else {
                    flag = true
                    return false
                  }
                })
              })
            }

            if (res2 && res3) {
              if (c.id * 1 > 1555179956843) {
                c.id = ((c.id + '').substring(0, 13) + Math.floor(Math.random() * 10000000)) * 1
                c.date = Date.now()
                return true
              } else {
                flag = true
                return false
              }
            }
            else return false
          })


          impchar(char)

          setJishao('导入已完成')

          if (flag) {
            setShowDiolag(true)
            setDioContent('数据存在错误，已导入正确部分')
            // alert('数据存在错误，已导入正确部分')
          }
          else {
            setShowDiolag(true)
            setDioContent('导入成功')
            // alert('导入成功')
          }
          input.current.value = ''
        }
      }
    } else {
      setJishao('请添加数据文件')
      // alert('请先添加要导入的数据文件')
      setShowDiolag(true)
      setDioContent('请先添加要导入的数据文件')
    }
  }

  return (
    <div className='zhezhao'>
      <div className='topbar'>
        <Link to="/addchar" data-title="添加角色"></Link>
        <div onClick={changeColor}><img src={require('../../assets/颜色.png')} alt="" /></div>
        <div onClick={showbar}><img src={require('../../assets/导入.png')} alt="" /></div>
      </div>

      {show ? <div className='bar'>
        <div onClick={showbar}><img src={require('../../assets/错误.png')} alt="" /></div>
        <div className='jieshao'>{jieshao}</div>

        {showDiolag && <Dialog title='提示' showFn={setShowDiolag}>{dioContent}</Dialog>}
        {/* <Dialog title='提示' onOk={setShowDiolag}>{dioContent}</Dialog> */}

        {inpshow ? <input ref={input} type="file" id="file" /> : ''}
        <a ref={a} href="#"></a>

        <div onMouseEnter={come} onClick={exp} className='btn'>导出数据</div>
        <div onMouseEnter={come} onClick={imp} className='btn'>导入数据</div>
      </div> : ''}
    </div>

  )
})



// mapDispatchToProps 简写, 和函数形式一样使用
const mapDispatch = {
  impchar: createImpchar,
  // delchar: createDelchar,
  // changechar: createChangechar,
  // jiaAsync: createAsyncAdd
}

// 使用connect创建容器组件, 再用其返回值连接ui组件
// export default connect(mapStateToProps, mapDispatchToProps)(MyRedux)
export default connect(mapStateToProps, mapDispatch)(TopBar)

// 返回的对象作为props传递给ui组件, 传递状态 ===> <UI a={1} />
function mapStateToProps(state) {
  return { chars: state.addchar_reducer }
}