import React, { useRef, useEffect, useState, memo } from 'react'
import './index.css'
export default memo(function MySelect({ style, children, val, fun, type, i, pi, shallow }) {

  const selectwrapper = useRef()
  let [len, setLen] = useState(0)
  let [txt, setTxt] = useState('')
  const list = useRef()
  const inp = useRef()
  const temp = useRef()
  const myselect = useRef()

  useEffect(() => {
    // console.log(children);
    let num = list.current.children.length
    len = Math.floor((num * 2.6 + 2 - 0.2) * 10) / 10 + 'rem'
    setLen(len)
    // selectwrapper.current.style.height = num*2.6 + 2 - 0.2 + 'rem'
  }, [])

  useEffect(() => {
    // console.log(children);
    // txt = children

    children.map((c) => {
      // console.log(c, val);
      if (c.props && c.props.value * 1 === val * 1) {
        setTxt(c.props.children)
        return
      }
      if (c.length) {
        c.map((p) => {
          if (p.props && p.props.value * 1 === val * 1) {
            setTxt(p.props.children)
          }
        })
        return
      }
    })

  }, [val])


  function click(e) {

    // temp.current.focus()

    selectwrapper.current.style.height === len ? temp.current.blur()
      : temp.current.focus()

    // console.log(temp.current.value);
    // selectwrapper.current.style.height = selectwrapper.current.style.height === len ? '0' : len
    // selectwrapper.current.style.height = len
    // console.log(e.target.value);
    // if (e.target.value !== undefined) {
    //   // temp.current.value = e.target.value
    //   console.log(temp.current.value);
    // }
    e.stopPropagation()
    e.preventDefault()
  }


  function click2(e) {
    // getAttribute
    fun(type, e, i, pi)
    e.stopPropagation()
    e.preventDefault()
    temp.current.blur()
  }

  function b() {
    // console.log('blur');
    selectwrapper.current.style.height = 0
  }
  function f() {
    // console.log('focus');
    selectwrapper.current.style.height = len
  }



  return (
    // <div className='descript lefttop border'>
    //     说明
    // </div>
    <div ref={myselect} className={style ? style + ' myselect' : 'myselect'}>
      <input type="text" className='temp' ref={temp} onChange={(e) => fun(e)} onBlur={b} onFocus={f} />
      <div className='selectwrapper' ref={selectwrapper}>
        <div className={style ? style + ' input' : 'input'} onMouseDown={click}>
          <div ref={inp} className='selectshow' >{txt}</div>
          <div className={shallow ? 'shallow arrow' : 'arrow'} ></div>
          <div className="list" >
            <ul ref={list} onMouseDown={click2} className='ul' id='ul'>
              {children}
              {/* <li value={0}>1</li>
                <li value={1}>2</li>
                <li value={2}>3</li> */}
            </ul>
          </div>
        </div>
      </div>
    </div>


  )
})
