import React, { useEffect, useRef } from 'react'
import { memo } from 'react'
import './index.css'
export default memo(function Tips({ children, css, maxwidth, po, top, top2 }) {

  let txtwrapper = useRef()


  useEffect(() => {
    let len = 0
    let timer = setTimeout(() => {
      children.map ? children.map((p) => {
        if (p.length) len += p.length
      })
        : len = children.length
      if (maxwidth && maxwidth < len) len = maxwidth
      // console.log(children, len);
      txtwrapper.current.style.width = len * 1.36 + 'rem'
      if(po){
        txtwrapper.current.style.left = len*1.36/2+'rem'
      }
      if(top){
        txtwrapper.current.style.top = '-3rem'
      }
      if(top2){
        txtwrapper.current.style.top = '-6rem'
      }
    }, 600);

    return () => {
      clearTimeout(timer)
    }
  }, [])

  return (
    <div className={(css ? css + ' ' : '') + 'tips'} >
      <img src={require('../../assets/帮助.png')} />
      <div className={'txtwrapper'} ref={txtwrapper}>
        {children}
      </div>
    </div>
  )
})