import React, { useEffect } from 'react'
import { Route, Routes, Navigate, Link, Outlet, useParams, useRoutes, } from 'react-router-dom'


import Router from '../Router'
export default function MyRoute() {

  useEffect(() => {
    // console.log('阻止滚轮事件');
    // 阻止滚轮默认事件, 也可设置0.0001的step, 并对输入的数值进行处理
    function stopS(e) {
      e.preventDefault();
    }
    // function stopM(e) {
    //   e.stopPropagation()
    // }
    let timer = setTimeout(() => {
      for (const ipt of document.querySelectorAll("input[type='number']")) {
        ipt.addEventListener('wheel', stopS, {
          passive: false, //  禁止 passive 效果
        })
      }

    }, 500);

    return () => {
      clearTimeout(timer)
      for (const ipt of document.querySelectorAll("input[type='number']")) {
        ipt.removeEventListener('wheel', stopS)
      }

    }
  })

  return (
    <div className='myroute'>
      {useRoutes(Router)}
    </div>
  )
}
