import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { getter } from '../../utils'
import './index.css'
export default function SSuit({ char, isdamage }) {

  let [total, setTotal] = useState(0)
  let history = useNavigate()
  const suit = ['outfitGrade_sword', 'outfitGrade_head', 'outfitGrade_armor', 'outfitGrade_necklace', 'outfitGrade_ring', 'outfitGrade_shoe']

  useEffect(() => {
    let res = 0
    if (char.id) {
      suit.map((e) => {
        res += Math.floor(getter(e, char) * 10) / 10
      })
      if (res !== total) {
        setTotal(res)
      }
    }
  }, [char])

  function tosuit() {
    history('/suit/' + char.id)
  }
  function todamage() {
    history('/damage/' + char.id)
  }

  // 显示装等分数
  return (
    <div className='ssuit lefttop border'>
      <div className='base'>
        <div>
          <h3>{isdamage ? char.infor.cName.substr(0, 11) : '角色面板'}</h3>
          {isdamage ? <h3 onClick={tosuit}>前往装备选择</h3> : <h3 onClick={todamage}>前往伤害模拟</h3>}
        </div>
        <div>
          <img src={require('../../assets/攻击力.png')} alt="" />
          <span>{parseInt(getter('wait_atk', char))}</span>
          <div>
            <img src={require('../../assets/总共.png')} title='总分' alt="" />
            <span>{!isdamage ? parseInt(total * 10) / 10 : parseInt(total+ getter('extraAttr', char))}
              {isdamage && getter('extraAttr', char) ? <span className='danger'>({parseInt(getter('extraAttr', char))})</span> : ''}
            </span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/防御力.png')} alt="" />
          {parseInt(getter('wait_def', char))}
          <div>
            <img src={require('../../assets/平均.png')} title='均分' alt="" />
            <span>{!isdamage ? parseInt(total * 10 / 6) / 10 : parseInt((total + getter('extraAttr', char)) / 6)}
              {isdamage && getter('extraAttr', char) ? <span className='danger'>({parseInt(getter('extraAttr', char) * 10 / 6) / 10})</span> : ''}
            </span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/生命值.png')} alt="" />
          {parseInt(getter('wait_hp', char))}
          <div>
            <img src={require('../../assets/大剑.png')} alt="" />
            <span>{parseInt(getter(suit[0], char) * 10) / 10}</span>
          </div>
          
        </div>
        <div>
          <img src={require('../../assets/速度.png')} alt="" />
          {parseInt(getter('wait_speed', char))}
          <div>
            <img src={require('../../assets/头盔.png')} alt="" />
            <span>{parseInt(getter(suit[1], char) * 10) / 10}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/暴击率.png')} alt="" />
          {parseInt(getter('wait_critRate', char) * 100)}%
          <div>
            <img src={require('../../assets/衣服.png')} alt="" />
            <span>{parseInt(getter(suit[2], char) * 10) / 10}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/爆伤.png')} alt="" />
          {parseInt(getter('wait_critDamage', char) * 100)}%
          <div>
            <img src={require('../../assets/项链.png')} alt="" />
            <span>{parseInt(getter(suit[3], char) * 10) / 10}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/命中.png')} alt="" />
          {parseInt(getter('wait_effect', char) * 100)}%
          <div>
            <img src={require('../../assets/戒指.png')} alt="" />
            <span>{parseInt(getter(suit[4], char) * 10) / 10}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/抵抗.png')} alt="" />
          {parseInt(getter('wait_resist', char) * 100)}%
          <div>
            <img src={require('../../assets/鞋子.png')} alt="" />
            <span>{parseInt(getter(suit[5], char) * 10) / 10}</span>
          </div>
        </div>
      </div>
    </div>
  )
}
