import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { getter } from '../../utils'
import './index.css'
export default function SSuit({ char, isdamage }) {

  let [total, setTotal] = useState(0)
  let history = useNavigate()
  const suit = ['outfitGrade_sword', 'outfitGrade_head', 'outfitGrade_armor', 'outfitGrade_necklace', 'outfitGrade_ring', 'outfitGrade_shoe']

  useEffect(() => {
    let res = 0
    if (char.id) {
      suit.map((e) => {
        res += Math.floor(getter(e, char) * 10) / 10
      })
      if (res !== total) {
        setTotal(res)
      }
    }
  }, [char])

  function tochoose() {
    history('/choose')
  }

  // 显示装等分数
  return (
    <div className='sdamage leftbottom border'>
      <div className='base'>
        <div>
          <h3><span>属性</span><span>预期</span><span>最终</span></h3>
          <h3 onClick={tochoose}>前往角色选择</h3>
        </div>
        <div>
          <img src={require('../../assets/攻击力.png')} alt="" />
          <span>{parseInt(getter('wait_atk', char) + (char.extraAttr.extraAttr_atk / 100 * getter('base_atk', char) * (char.base.baseBonus.baseBonus_atk[1]/100 + 1)))}
            {char.extraAttr.extraAttr_atk ?
              <span className='danger'>({parseInt(char.extraAttr.extraAttr_atk / 100 * getter('base_atk', char) * (char.base.baseBonus.baseBonus_atk[1]/100 + 1) * 10) / 10})</span>
              : ''}
          </span>
          <div>
            <span>{parseInt(getter('final_atk', char))}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/防御力.png')} alt="" />
          <span>{parseInt(getter('wait_def', char) + char.extraAttr.extraAttr_def / 100 * char.base.base_def)}
          {char.extraAttr.extraAttr_def ?
            <span className='danger'>({parseInt(char.extraAttr.extraAttr_def / 100 * char.base.base_def * 10) / 10})</span>
            : ''}
            </span>
          
          <div>
            <span>{parseInt(getter('final_def', char))}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/生命值.png')} alt="" />
          <span>
            {parseInt(getter('wait_hp', char) + char.extraAttr.extraAttr_hp / 100 * getter('base_hp', char) * (char.base.baseBonus.baseBonus_hp[1] + 1))}
          {char.extraAttr.extraAttr_hp ?
            <span className='danger'>({parseInt(char.extraAttr.extraAttr_hp / 100 * getter('base_hp', char) * (char.base.baseBonus.baseBonus_hp[1] + 1) * 10) / 10})</span>
            : ''}
          </span>
          <div>
          <span>{parseInt(getter('final_hp', char))}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/速度.png')} alt="" />
          <span>
            {parseInt(getter('wait_speed', char) + char.extraAttr.extraAttr_speed)}
          {char.extraAttr.extraAttr_speed ?
            <span className='danger'>({parseInt(char.extraAttr.extraAttr_speed * 10) / 10})</span>
            : ''}
          </span>
          <div>
            <span>{parseInt(getter('final_speed', char))}</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/暴击率.png')} alt="" />
          <span>
            {parseInt(getter('wait_critRate', char) * 100 + char.extraAttr.extraAttr_critRate)}%
          {char.extraAttr.extraAttr_critRate ?
            <span className='danger'>({char.extraAttr.extraAttr_critRate})</span>
            : ''}
          </span>
          <div>
          </div>
        </div>
        <div>
          <img src={require('../../assets/爆伤.png')} alt="" />
          <span>
            {parseInt(getter('wait_critDamage', char) * 100 + char.extraAttr.extraAttr_critDamage)}%
          {char.extraAttr.extraAttr_critDamage ?
            <span className='danger'>({char.extraAttr.extraAttr_critDamage})</span>
            : ''}
          </span>
          <div>
          <span>{parseInt(getter('final_critDamage', char)*100)}%</span>
          </div>
        </div>
        <div>
          <img src={require('../../assets/命中.png')} alt="" />
          <span>
            {parseInt(getter('wait_effect', char) * 100 + char.extraAttr.extraAttr_effect)}%
          {char.extraAttr.extraAttr_effect ?
            <span className='danger'>({char.extraAttr.extraAttr_effect})</span>
            : ''}
          </span>
          <div>
          </div>
        </div>
        <div>
          <img src={require('../../assets/抵抗.png')} alt="" />
          <span>
            {parseInt(getter('wait_resist', char) * 100 + char.extraAttr.extraAttr_resist)}%
          {char.extraAttr.extraAttr_resist ?
            <span className='danger'>({char.extraAttr.extraAttr_resist})</span>
            : ''}
          </span>
          <div>
          </div>
        </div>
      </div>
    </div>
  )
}
