function getData() {
    let res = window.localStorage.getItem('E7Simulator')
    // console.log(res);
    if (res !== null){
        return JSON.parse(res)
    }else{
        return []
    }
}

function setData(data) {
    window.localStorage.setItem('E7Simulator', JSON.stringify(data))
}

export default (state = getData(), action) => {
    const { type, data } = action

    switch (type) {
        case 'addchar':
            // console.log([...state, data]);
            setData([data, ...state])
            return [data, ...state]

        case 'impchar':
            // console.log([...state, data]);
            setData([...data, ...state])
            return [...data, ...state]

        case 'delchar':
            const res = state.filter((e) => {
                return e.id !== data
            })
            setData(res)
            return res

        case 'changechar':
            let index = -1
            state.forEach((e, i) => {
                if (e.id === data.id) index = i
            });
            const res2 = [...state]
            res2[index] = data
            if (index === -1) console.log('error', type)
            setData(res2)
            return res2

        default:
            return state
    }
}