import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { getCharsort } from '../../utils'
import './index.css'

export default function SChoose({ char, chars, issuit }) {
  // 基础面板展示
  let [charsort, setCharsort] = useState({})
  let [isShow, setIsShow] = useState(false)
  // let [issuit, setissuit] = useState(false)
  let [atk, setAtk] = useState(1)
  let [hp, setHp] = useState(1)
  let [def, setDef] = useState(1)
  let [speed, setSpeed] = useState(1)
  const rank = ['第一', '第二', '第三']
  let history = useNavigate()
  function toAddchar() {
    history('/choose')
  }
  function toSuit() {
    history('/suit/' + char.id)
  }

  useEffect(() => {
    const res = getCharsort(chars)
    setCharsort(res)

    if (char && char.id && chars.length >= 1) {
      setAtk(res.atk.indexOf(char.base.base_userAtk) + 1)
      setHp(res.hp.indexOf(char.base.base_userHp) + 1)
      setDef(res.def.indexOf(char.base.base_def) + 1)
      setSpeed(res.speed.indexOf(char.base.base_speed) + 1)
      setIsShow(true)
    }

  }, [char])

  return (
    <div className='schoose lefttop border'>
      {
        isShow ?
          <div className='base'>
            <div>
              <h3>{char.infor.cName.substr(0, 11)}</h3>
              {issuit? <h3 onClick={toAddchar}>前往角色选择</h3>
              :<h3 onClick={toSuit}>前往装备选择</h3>}
            </div>
            <div>
              <img src={require('../../assets/攻击力.png')} alt="" />
              <span>{char.base.base_userAtk}</span>
              <div>
                <img src={require(`../../assets/${rank[Math.floor((atk - 1) / charsort.atk.length * 3)]}.png`)}
                  title={'前' + Math.floor((atk / charsort.atk.length) * 100) + '%'} alt="" />
                <span>{atk}</span>
              </div>
            </div>
            <div>
              <img src={require('../../assets/防御力.png')} alt="" />
              {char.base.base_def}
              <div>
                <img src={require(`../../assets/${rank[Math.floor((def - 1) / charsort.def.length * 3)]}.png`)}
                  title={'前' + Math.floor((def / charsort.def.length) * 100) + '%'} alt="" />
                <span>{def}</span>
              </div>
            </div>
            <div>
              <img src={require('../../assets/生命值.png')} alt="" />
              {char.base.base_userHp}
              <div>
                <img src={require(`../../assets/${rank[Math.floor((hp - 1) / charsort.hp.length * 3)]}.png`)}
                  title={'前' + Math.floor((hp / charsort.hp.length) * 100) + '%'} alt="" />
                <span>{hp}</span>
              </div>
            </div>
            <div>
              <img src={require('../../assets/速度.png')} alt="" />
              {char.base.base_speed}
              <div>
                <img src={require(`../../assets/${rank[Math.floor((speed - 1) / charsort.speed.length * 3)]}.png`)}
                  title={'前' + Math.floor((speed / charsort.speed.length) * 100) + '%'} alt="" />
                <span>{speed}</span>
              </div>
            </div>
            <div>
              <img src={require('../../assets/暴击率.png')} alt="" />
              {char.base.base_critRate }%
              <div></div>
            </div>
            <div>
              <img src={require('../../assets/爆伤.png')} alt="" />
              {char.base.base_critDamage}%
              <div></div>
            </div>
            <div>
              <img src={require('../../assets/命中.png')} alt="" />
              {char.base.base_effect * 100}%
              <div></div>
            </div>
            <div>
              <img src={require('../../assets/抵抗.png')} alt="" />
              {char.base.base_resist * 100}%
              <div></div>
            </div>
          </div>
          : <h3>请选择角色</h3>
      }
    </div>
  )
}
