import React, { useRef, useEffect, useState, memo } from 'react'
import './index.css'
export default memo(function MyInput({ style, children, val, defo, i, pi, fun, type, text, shallow }) {

  const inputwrapper = useRef()
  let [len, setLen] = useState(0)
  // let [val, setVal] = useState(0)
  const list = useRef()
  const inp = useRef()

  useEffect(() => {
    let num = list.current.children.length
    len = Math.floor((num * 2.6 + 2) * 10) / 10 + 'rem'
    setLen(len)
    // inputwrapper.current.style.height = num*2.6 + 2 - 0.2 + 'rem'
  }, [])

  function click(e) {

    inputwrapper.current.style.height === len ? inp.current.blur()
      : inp.current.focus()

    e.stopPropagation()
    e.preventDefault()
  }

  function click2(e) {
    fun(type, e, i, pi)

    e.stopPropagation()
    e.preventDefault()
    inp.current.blur()
  }

  function b() {
    // console.log('blur');
    inputwrapper.current.style.height = 0
  }
  function f(e) {
    // console.log('focus');
    e.target.select()
    inputwrapper.current.style.height = len
  }

  function change(e) {
    // console.log(233);
    fun(type, e, i, pi)
  }

  return (
    // <div className='descript lefttop border'>
    //     说明
    // </div>
    <div className={style ? style + ' myinput' : 'myinput'}>
      <div className='inputwrapper' ref={inputwrapper}>

        <div className={style ? style + ' input' : 'input'}>
          {text ? <input type="text" ref={inp} onFocus={f} onBlur={b} onInput={change} value={val} placeholder={defo} />
            : <input type="number" ref={inp} onFocus={f} onBlur={b} onInput={change} value={val} placeholder={defo} />}
          <div className={shallow ? 'shallow arrow' : 'arrow'} onMouseDown={click}></div>
          <div className="list" onMouseDown={click2}>
            <ul ref={list}>
              {children}
            </ul>
          </div>
        </div>

      </div>
    </div>


  )
})
