import { createStore, combineReducers } from 'redux'

// 引入redux开发者工具所需的中间件
// import {composeWithDevTools} from 'redux-devtools-extension'

// 异步action需要的中间件
// import thunk from 'redux-thunk'

// 单reducer时无需合并
// import count from './reducers/count_reducer'

// 引入多个reducer
import addchar_reducer from './addchar/reducer.js'
// import choose_reducer from './choose/reducer'
// import damage_reducer from './damage/reducer'
// import suit_reducer from './suit/reducer'

// 合并reducers, 传入的这个对象即为保存的state, 即此处state为 {countAbout: {count:0}, otherAbout:{a:1}}
const reducers = combineReducers({
    addchar_reducer,
    // choose_reducer,
    // damage_reducer,
    // suit_reducer
})



// 一般的store
// export default createStore(count)

// 可以解析异步action的store
// export default createStore(count, applyMiddleware(thunk))

// 经过合并的reducers
export default createStore(reducers)

// 使用开发者工具, 若不需要异步action, 则直接传入 composeWithDevTools() 即可
// export default createStore(reducers, composeWithDevTools(applyMiddleware(thunk)))
