import { Navigate } from 'react-router-dom'

// 引入组件
import BChoose from '../pages/BChoose'
import BDamage from '../pages/BDamage'
import BSuit from '../pages/BSuit'
import AddChar from '../pages/AddChar'
// import Tips from '../components/Tips'
// import MyInput from '../components/MyInput'
// import MySelect from '../components/MySelect'
import Dialog from '../components/Dialog'
export default [
    {
        path: '/',
        element: <BChoose />
    },
    {
        path: 'addchar',
        element: <AddChar />
    },
    {
        path: 'addchar/:id',
        element: <AddChar />
    },
    {
        path: 'suit/:id',
        element: <BSuit />,
    },
    {
        path: 'suit',
        element: <BSuit />,
    },
    {
        path: 'damage',
        element: <BDamage />
    },
    {
        path: 'd',
        element: <Dialog />
    },
    {
        path: 'damage/:id',
        element: <BDamage />
    },
    {
        path: '/*',
        element: <Navigate to={'/'} />
    },
]