import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { useNavigate } from 'react-router-dom'

import SChoose from '../../components/SChoose'
// import SDamage from '../../components/SDamage'
import Descript from '../../components/Descript'
// import SSuit from '../../components/SSuit'
import TopBar from '../../components/TopBar'
// import Tips from '../../components/Tips'
// import MySelect from '../../components/MySelect'
import { debounce } from '../../utils'


import './style.css'
function BChoose(props) {

  let history = useNavigate()

  function imgOnError(e, i) {
    e.target.src = require('../../assets/透明.png')
    // function r(max, min) {
    //   return Math.floor(Math.random() * (max - min) + min)
    // }
    function r() {
      return Math.floor(Math.random() * 140 + 20)
    }
    e.target.style.background = `rgb(${r()},${r()},${r()})`

    // if (i === 0) { // red
    //   e.target.style.background = `rgb(${r(234, 180)},${r(70, 0)},${r(70, 0)})`
    // }
    // if (i === 1) { // blue
    //   e.target.style.background = `rgb(${r(70, 0)},${r(70, 0)},${r(234, 180)})`
    // }
    // if (i === 2) { // green
    //   e.target.style.background = `rgb(${r(70, 0)},${r(234, 180)},${r(70, 0)})`
    // }
    // if (i === 4) { // gold
    //   e.target.style.background = `rgb(${r(254, 220)},${r(215, 165)},${r(150, 0)})`
    // }
    // if (i === 3) { // dark
    //   e.target.style.background = `rgb(${r(190, 160)},${r(80, 0)},${r(140, 180)})`
    // }

    e.target.setAttribute('class', 'err')
  }

  const star = ['空星', '半星', '实星']
  const occ = ['战士', '法师', '盗贼', '骑士', '射手', '精灵']
  const moonlight = ['火', '水', '草', '暗', '光']

  let [choose, setChoose] = useState([-1, -1, -1])
  let [char, setChar] = useState([])
  let [hoverchar, setHoverchar] = useState()
  // let [charsort, setCharsort] = useState({})


  useEffect(() => {
    setChar([...props.chars.addchar_reducer])
    // setCharsort(getCharsort(props.chars.addchar_reducer))
  }, [props.chars.addchar_reducer])

  useEffect(chooseChange, [choose])

  function chooseChange() {
    const res = props.chars.addchar_reducer.filter((e) => {
      return (e.infor.occ === choose[1] || choose[1] === -1)
        && (e.infor.moonlight === choose[2] || choose[2] === -1) && (e.infor.star === choose[0] + 3 || choose[0] === -1)
    })
    setChar(res)
  }

  function jump(type, i) {
    if (type === 'star') {
      setChoose([i, choose[1], choose[2]])
    }
    if (type === 'occ') {
      setChoose([choose[0], i, choose[2]])
    }
    if (type === 'moonlight') {
      setChoose([choose[0], choose[1], i])
    }
  }
  function toSuit(id) {
    history('/suit/' + id)
  }
  function toAddChar(id, e) {
    e.stopPropagation()
    history('/addchar/' + id)
  }
  function search(e) {
    if (e.target.value === '') chooseChange()
    // console.log(e.target.value);
    if (e.code === 'Enter') {
      chooseChange()
      if (e.target.value !== '') {
        const res = char.filter((c) => {
          return c.infor.cName.includes(e.target.value)
        })
        setChar(res)
      }
    }
  }

  function fn(id) {
    const res = char.filter((c) => {
      return c.id === id * 1
    })
    setHoverchar(...res)
  }
  // 传入函数后返回有防抖功能的函数
  const sethoverchar = debounce(fn)

  function toSChoose(id) {
    sethoverchar(id)
  }


  // function click() {
  //   history('/addchar')
  // }

  return (
    <div className='bchoose wrapper'>
      <div className='left'>
        <TopBar></TopBar>
        <SChoose char={hoverchar} chars={props.chars.addchar_reducer}></SChoose>
        <Descript></Descript>
      </div>
      <div className='main border'>
        <h2>选择角色</h2>
        {/* <button onClick={click}>+</button> */}
        <div className='choose'>
          <div className='nav'>
            <input className='search' type="text" onKeyUp={search} placeholder='搜索' />
            <div onClick={() => jump('star', -1)} className={choose[0] === -1 ? 'iconchoose' : ''}><img src={require('../../assets/全部.png')} /></div>
            {
              star.map((e, i) => {
                let title = ['三星', '四星', '五星']
                return (
                  <div key={i} onClick={() => jump('star', i)}
                    className={choose[0] === i ? 'iconchoose' : ''}><img title={title[i]} src={require(`../../assets/${e}.png`)} /></div>
                )
              })
            }
            <div className='hr'>hr</div>
            <div onClick={() => jump('occ', -1)} className={choose[1] === -1 ? 'iconchoose' : ''}><img src={require('../../assets/全部.png')} /></div>
            {
              occ.map((e, i) => {
                return <div key={i + 4} onClick={() => jump('occ', i)} className={choose[1] === i ? 'iconchoose' : ''}><img src={require(`../../assets/${e}.png`)} /></div>
              })
            }
            <div className='hr'>hr</div>
            <div onClick={() => jump('moonlight', -1)} className={choose[2] === -1 ? 'iconchoose' : ''}><img src={require('../../assets/全部.png')} /></div>
            {
              moonlight.map((e, i) => {
                return <div key={i + 10} onClick={() => jump('moonlight', i)} className={choose[2] === i ? 'iconchoose' : ''}><img src={require(`../../assets/${e}.png`)} /></div>
              })
            }
          </div>
          <ul className='row'>
            {
              char.length > 0 ? char.map((c, i) => {
                return (
                  <li onMouseEnter={() => toSChoose(c.id)} className='col-3 sborder' key={c.id} onClick={() => toSuit(c.id)}>
                    <img onError={imgOnError} src={c.infor.charIcon} />
                    <p txt={c.infor.cName[0].toUpperCase()}></p>
                    <img src={require(`../../assets/${occ[c.infor.occ]}.png`)} />
                    <img src={require(`../../assets/${moonlight[c.infor.moonlight]}.png`)} />
                    <div>{c.infor.cName}</div>
                    <div className='row'>
                      <div className='col-6'><img src={require('../../assets/攻击力.png')} />{c.base.base_userAtk}</div>
                      <div className='col-6'><img src={require('../../assets/防御力.png')} />{c.base.base_def}</div>
                      <div className='col-6'><img src={require('../../assets/生命值.png')} />{c.base.base_userHp}</div>
                      <div className='col-6'><img src={require('../../assets/速度.png')} />{c.base.base_speed}</div>
                    </div>
                    <img src={require('../../assets/齿轮.png')} title='修改' onClick={(e) => toAddChar(c.id, e)} />
                  </li>
                )
              })
                : <div><br /><br /><h2 style={{ 'whiteSpace': 'nowrap', 'marginLeft': '2rem' }}>暂无数据</h2></div>
            }


          </ul>
        </div>
      </div >
    </div >
  )
}

// mapDispatchToProps 简写, 和函数形式一样使用
const mapDispatch = {
  // changechar: createChangechar,
  // jiaAsync: createAsyncAdd
}

// 使用connect创建容器组件, 再用其返回值连接ui组件
// export default connect(mapStateToProps, mapDispatchToProps)(MyRedux)
export default connect(mapStateToProps, mapDispatch)(BChoose)

// 返回的对象作为props传递给ui组件, 传递状态 ===> <UI a={1} />
function mapStateToProps(state) {
  return { chars: state }
}
