import React, { useEffect } from 'react';
import { createPortal } from 'react-dom';
import './index.css';

const Dialog = ({ dialogWidth, title, children, showFn, cancle, ok, cancleArg, okArg , cancelText, sureText }) => {
    // let [n, setN] = useState(document.createElement('div'))
    const node = document.createElement('div');

    useEffect(() => {
        document.body.appendChild(node);
        return () => {
            document.body.removeChild(node);
        };
    }, []);

    function oncCancle () {
        showFn(false)
        cancle && cancle(cancleArg)
    }

    function onOk () {
        ok && ok(okArg)
        showFn(false)
    }

    
    return createPortal(
        <div className="dialog">
            <div className="dialog-inner" style={{ width: dialogWidth || '23rem' }}>
                <div className="dialog-title">{title}</div>
                {/* <div className="dialog-title">镖旗打完阿斯顿</div> */}
                <div className="content">{children}</div>
                {/* <div className="content">内容内容按当时啊阿迪王</div> */}

                {ok ?
                    <div className="btns">
                        <div onClick={oncCancle}>{cancelText || '取消'}</div>
                        <div onClick={onOk}>{sureText || '确定'}</div>
                    </div> :
                    <div className="btns">
                        <span onClick={oncCancle}>{sureText || '确定'}</span>
                        {/* <div onClick={onCancle}>{cancelText || '取消'}</div>
                        <div onClick={onOk}>{sureText || '确定'}</div> */}
                    </div>
                }
            </div>
        </div>,
        node
    )
}

export default Dialog;
